import { Box, Button, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalOverlay, Text, VStack } from '@chakra-ui/react'

interface SelectTokenAnddFeeConfirmProps {
  title: string
  subTitle?: string
  btnText: string
  isOpen: boolean
  onClose: () => void
  onConfirm: () => void
}

function SelectTokenAnddFeeConfirm({ title, subTitle, btnText, isOpen, onClose, onConfirm }: SelectTokenAnddFeeConfirmProps) {
  return (
    <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalCloseButton />
        <ModalBody p="20px 16px">
          <VStack>
            <Box w="140px" h="140px" bg="center / contain no-repeat  url('/images/img_pool@2x.png')" />
            <Text h="20px" lineHeight="20px" fontSize="14px" color="text_caption" fontWeight="500">
              {title}
            </Text>
            {subTitle && <Text fontSize="14px">{subTitle}</Text>}
          </VStack>
        </ModalBody>

        <ModalFooter p="0 16px 16px">
          <Button
            w="100%"
            h="40px"
            borderRadius="12px"
            colorScheme="blue"
            fontSize="16px"
            fontWeight="500"
            onClick={() => {
              onClose()
              onConfirm()
            }}
          >
            {btnText}
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  )
}

export default SelectTokenAnddFeeConfirm
